* {
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  display: flex;
  min-height: 100vh;
}

.task {
  flex-grow: 1;
  flex-basis: 80%;
}

