.flex {
  display: flex;
}

.flex_column {
  max-width: 550px;
}


.market_item {
  min-width: 120px;
  width: 18%;
  position: relative;
  list-style: none;
  font-size: 1.15em;
  margin-right: 10px;
  margin-bottom: 20px;
}
.market_item:last-child {
  margin-right: 0;
}

.market_item__img {
  width: 100%;
}

.discount_icon {
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: rgb(230, 0, 0);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 3px;
  left: 3px;
  color: white;
  font-size: 1.20em;
}
.discount_icon__text {
  font-size: 0.7em;
  font-weight: 700;
  transform: rotate(30deg);
}

.market_item__price_wrapper span:first-child {
  font-size: 1.15em;
  font-weight: bold;
  margin-right: 5px;
}
.market_item__old_price {
  font-size: .9rem;
  color: gray;
  text-decoration: line-through;
}

.market_item__link {
  text-decoration: none;
}

.market_list {
  display: flex;
  flex-wrap: wrap;
  min-width: 700px;
  width: 80%;
  margin: auto;
  max-width: 1000px;
  justify-content: space-evenly;
  padding: .5rem .7rem;
}

.add_item_form {
  padding: .5rem 1rem;
  display: flex;
  flex-direction: column;
  width: 30%;
  min-width: 300px;
  margin: auto;
}

.form_select,
.form_input {
  padding: .3rem .6rem;
  margin-bottom: 5px;
}

.form_submit_btn {
  padding: .4rem .5rem;
  float: right;
}

.file_input {
  visibility: hidden;
  position: absolute;
  top: -1000px;
}

.image_preview {
  width: 80%;
  box-sizing: border-box;
  padding: .3em .5em;
  margin: auto;
}

.navigation {
  background-color: rgb(240, 240, 240);
  position: relative;
  min-width: 52px;
  max-width: 270px;
}

.nav_icon {
  padding: .5rem;
  font-size: 36px;
  position: absolute;
  right: 0;
  cursor: pointer;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  width: 100%;
}

.navigation__list a {
  text-decoration: none;
  color: black;
  font-size: 1.25rem;
  text-transform: uppercase;
  padding: .5rem 1rem;
}

.navigation_hidden {
  display: none;
}

